<template>
  <div style="text-align:center">
    <input type="text" placeholder="제목" v-model="Qna.title" />
    <input type="text" placeholder="아이디" v-model="Qna.id" />
    <input type="text" placeholder="내용입력" v-model="Qna.contents" />
    <span @click="addTodo">등록하기</span>
  </div>
</template>
<script>
import Constant from "../Constant";
export default {
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  methods: {
    addTodo() {
      if (this.Qna.contents.trim() != "") {
        this.$store.dispatch(Constant.ADD_Qna, { Qna: this.Qna });
        this.$router.push("/qna/");
        this.clear();
      }
    },
    clear() {
      (this.Qna.id = ""),
        (this.Qna.title = ""),
        (this.Qna.regdate = ""),
        (this.Qna.contents = "");
    }
  }
};
</script>

