<template>
  <div style="text-align:center">
    <h2>상세정보</h2>
    아이디 : {{Qna.id}}
    <br />
    번호 : {{Qna.no}}
    <br />
    내용 : {{Qna.contents}}
    <br />
    등록시간 : {{Qna.regdate}}
    <br />새로운 댓글을 입력 :
    <input v-model="newComment.comment" />
    <button @click="addComment(Qna.id,Qna.no)">등록</button>
    <div v-for="comment in CommentLists" v-bind:key="comment.no">
      {{comment.id}} : {{comment.comment}} : {{comment.cregdate}}
      <br />
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "detailpage",
  data() {
    return {
      Qna: {},
      CommentLists: [],
      newComment: {
        qno: 0,
        comment: "",
        id: ""
      }
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
      .then(() => {
        this.Qna = { ...this.$store.state.Qna };
      });
    console.log("Aa" + this.$route.params.no);
    this.$store
      .dispatch(Constant.GET_Comments, { qno: this.$route.params.no })
      .then(() => {
        this.CommentLists = { ...this.$store.state.CommentLists };
        console.log(this.CommentLists);
      });
  },
  methods: {
    addComment(id, qno) {
      if (this.newComment.comment.trim() != "") {
        this.newComment.qno = qno;
        this.newComment.id = id;
        this.$store.dispatch(Constant.ADD_COMMENT, {
          comment: this.newComment
        });
        this.$router.push("/qna/detail/" + qno);
      }
    }
  }
};
</script>

